$(function () {
	gsap.registerPlugin(ScrollTrigger, DrawSVGPlugin);
	gsap.defaults({ease: 'none'});

	if ($('.steps').length) { /* $(window).width() > 1279 */
		const main = gsap.timeline({defaults: {duration: .5, stagger: .5},
			scrollTrigger: {
				trigger: '.steps',
				// scrub: true,
				start: 'top+=30% center',
				end: 'bottom-=50% center',
				// markers: true
			}
		})
		.from('.steps__decor-line mask path', {
			drawSVG: 0
		}, 0)
		.to('.steps__decor-dot', {
			autoAlpha: 1
		}, 0)
		.to('.steps__item', {
			autoAlpha: 1
		}, 0)
	}
})