$(function () {
	$('.js-footer-link').click(function (e) {
		e.preventDefault();

		let id = $(this).attr('href');
		let top = $(id).offset().top;
		let headerHeight = $('.js-header').outerHeight();
		let padding = $(window).width() > 767 ? 20 : 16;

		$('html').animate({
			scrollTop: top - (headerHeight / 2 + padding)
		}, 1000);
	});
})