$(function () {
  const ticker = new Swiper('.js-ticker', {
    slidesPerView: 'auto',
    loop: true,
    allowTouchMove: false,
    autoplay: {
      delay: 0,
    },
    on: {
      init: function () {
        let speed = this.slides[0].childElementCount * 8000;
        this.params.speed = speed;
      },
    },
  });

  const aboutSlider = new Swiper('.js-about-slider', {
    slidesPerView: 'auto',
    speed: 1200,
    spaceBetween: 8,
    breakpoints: {
      768: {
        enabled: false,
        spaceBetween: 0,
      }
    },
    navigation: {
      nextEl: '.about-button-next',
      prevEl: '.about-button-prev'
    }
  });

  const advantagesSlider = new Swiper('.js-advantages-slider', {
    slidesPerView: 'auto',
    speed: 1200,
    spaceBetween: 8,
    breakpoints: {
      768: {
        enabled: false,
        spaceBetween: 0,
      }
    },
    navigation: {
      nextEl: '.advantages-button-next',
      prevEl: '.advantages-button-prev'
    }
  });

  const featureSlider = new Swiper('.js-feature-slider', {
    slidesPerView: 1.14,
    speed: 1200,
    spaceBetween: 16,
    breakpoints: {
      768: {
        slidesPerView: 'auto',
        enabled: false,
        spaceBetween: 0,
      }
    },
    navigation: {
      nextEl: '.feature-button-next',
      prevEl: '.feature-button-prev'
    }
  });

  const tariffsSlider = new Swiper('.js-tariffs-slider', {
    enabled: false,
    slidesPerView: 'auto',
    speed: 1200,
    breakpoints: {
      768: {
        enabled: true,
        spaceBetween: 16,
        initialSlide: 1,
        centeredSlides: true,
        centeredSlidesBounds: true
      },
      1366: {
        enabled: false,
        spaceBetween: 16,
        initialSlide: 1,
        centeredSlides: true,
        centeredSlidesBounds: true
      }
    }
  });
})